import { Axios } from 'axios'
import {
  VaultTypeData,
  VaultTypeRoutes,
  VaultTypesClient,
  VaultTypesSearchQuery,
} from '../vault-type.model'
import { stringify, uriFy } from '../../core'

/**
 * Create vault types client
 * @param axios - The Axios instance
 * @returns the vault type GOT client
 */
export function createVaultTypesClient(axios: Axios): VaultTypesClient {
  return {
    // Create new token
    create: (body: VaultTypeData) =>
      axios
        .request({
          url: VaultTypeRoutes.v1Create,
          method: 'POST',
          data: stringify(body),
        })
        .then((response) => response.data),

    // Search tokens
    search: (searchParams?: VaultTypesSearchQuery) =>
      axios
        .request({
          url: VaultTypeRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => response.data),

    // List tokens
    list: (searchParams?: VaultTypesSearchQuery) =>
      axios
        .request({
          url: VaultTypeRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => {
          const page = response.data
          return page.data
        }),
    findOne: (searchParams?: VaultTypesSearchQuery) =>
      axios
        .request({
          url: VaultTypeRoutes.v1Search,
          method: 'GET',
          params: searchParams
            ? new URLSearchParams(uriFy(searchParams))
            : undefined,
        })
        .then((response) => {
          const page = response.data
          return page.data ? page.data[0] : undefined
        }),
  }
}
